.site-links {
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 13px;
  padding-right: 20px;

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__link {
    display: block;

    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;

    font-family: $main-font;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: $basic-black;

    &:hover,
    &:focus {
      color: $accent-color;
    }

    &:active {
      color: $basic-grey;
    }
  }
}
